.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 130vh;
  background-color: rgb(252, 252, 252);
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #FFFFFF;
  border-radius: 8px;
  height: 60%;
  width: 35%;
}
.flex{
  display:flex;
  width:'700px';
  height: 130vh
}
.questiontab{
  width: '65%'
}
@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
  }

  .container {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 60%;
  }
}
